header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.logo-container {
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  padding: 0.5rem 1rem;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.blue-logo {
  height: 6.5vh;
  width: 3vw;
  margin-right: 0.3vw;
}

.marine-ops {
  height: 2.8vh;
  width: 7.3vw;
}

.user-info {
  position: fixed;
  top: 2%;
  right: 1%;
  display: flex;
  align-items: center;
  background: #0E1729;
  border-radius: 30px;
  color: white;
  font-weight: 400;
  font-size: 1.6vh;
  cursor: pointer;
  text-transform: capitalize;
}

.user-dropdown {
  position: relative;
}

.user-dropdown-toggle {
  background: #0E1729;
  border: 1px solid #1C263D;
  border-radius: 50px;
  padding: 0.9vh 0.7vw;
  color: white;
  font-weight: 400;
  font-size: 1.6vh;
  cursor: pointer;
  text-transform: capitalize;
  display: flex;
  align-items: center;
}

.user-icon {
  border-radius: 50%;
  margin-right: 0.5vw;
  border: 1px solid #1F2E50;
  padding: 0.3vw;
  background: #14213B;
  width: 1.71vw;
  height: 3.6vh;
}

.username {
  margin-right: 0.5vw;
  color: white;
  font-weight: 400;
  font-size: 1.6vh;
}

.caret-down {
  display: inline-block;
  font-size: 14px;
  color: white;
}

.caret-up {
  display: none;
  font-size: 14px;
  color: white;
}

/* On hover: hide down arrow, show up arrow */
.user-dropdown:hover .caret-down {
  display: none;
}

.user-dropdown:hover .caret-up {
  display: inline-block;
}

.user-dropdown-content {
  display: none;
  position: relative;
  border-radius: 50px;
  padding: 0;
  font-weight: 400;
  font-size: 1.6vh;
  cursor: pointer;
  text-transform: capitalize;
  z-index: 1;
}

.user-dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.user-dropdown:hover .user-dropdown-content {
  display: block;
}

.logout-button {
    display: flex;
    align-items: center;
    margin-top: 1vh;
    margin-bottom: 1vh;
    margin-left: 1.2vw;
    width: 100%;
    background: none;
    border: none;
    text-align: center;
    color: #FFFFFF;
    font-size: 1.6vh;
    font-weight: 400;
}

img.logout_icon {
  margin-right: 0.2vw;
}

@media (max-width: 768px) {
  .logo-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 4vw;
    width: 100%;
    padding: 2vh 5vw;
    box-sizing: border-box;
    margin: 0;
  }

  .blue-logo {
    width: clamp(40px, 18vw, 70px);
    height: auto;
  }

  .marine-ops {
    width: clamp(80px, 35vw, 140px);
    height: auto;
  }
}
