#cargoNameInput {
  font-weight: 400;
  font-size: 1.6vh;
  padding: 0.9vh 1vw;
  border: none;
  border-radius: 3vh 0 0 3vh;
  outline: none;
  background-color: transparent;
  width: 20vw;
  height:5vh;
  color: #7B869D;
}

#cargoNameInput::placeholder {
  color: #7B869D;
}


#cargoForm { --cargo-input-bg: #131D35; }

#cargoNameInput:-webkit-autofill,
#cargoNameInput:-webkit-autofill:hover,
#cargoNameInput:-webkit-autofill:focus {
  font-size: 1.6vh !important;                 
  -webkit-text-fill-color: #7B869D !important; 
  caret-color: #7B869D !important;
  /* paint over the UA autofill bg with your container bg */
  /* -webkit-box-shadow: 0 0 0 1000px var(--cargo-input-bg) inset !important;
  box-shadow: 0 0 0 1000px var(--cargo-input-bg) inset !important; */
  border-radius: 3vh 0 0 3vh;                  
  transition: background-color 9999s ease-out 0s; 
}

/* Firefox */
#cargoNameInput:-moz-autofill{
  font-size: 1.6vh !important;
  -moz-text-fill-color: #7B869D !important;
  box-shadow: 0 0 0 1000px var(--cargo-input-bg) inset !important;
  border-radius: 3vh 0 0 3vh;
  caret-color: #7B869D !important;
}

