#buttonContainers {
    position: absolute;
    top: 0vh;
    left: 0vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: linear-gradient(180deg, #97C6D4 0%, #003748 100%);
    width: 3vw;
    border-top-left-radius: 6px;
    padding-top: .8vh;
    padding-bottom: .8vh;
}

#buttonContainers button {
    background: #223151;
    border: none;
    border-radius: 21px;
    width: 2vw;
    height: 2vw;
    margin: 0.5vh;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.6em;
    color: white;
    position: relative;
}

img#view {
    transform: scale(1);
}

#buttonContainers button.active {
    border: 1px solid #017BCD;
}

/* Tooltip bubble */
#buttonContainers button::after {
  content: attr(data-tooltip);
  position: absolute;
  left: 120%;  /* appear to the right side of button */
  top: 50%;
  transform: translateY(-50%);
  background-color: #223151;
  color: #DEEAFD;
  font-family: 'Roboto';
  padding: 4px 8px;
  border-radius: 4px;
  white-space: nowrap;
  font-size: 1.2vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out;
  z-index: 10;
  width: 3vw; 
  text-align: center;  
}

#buttonContainers button:hover::after,
#buttonContainers button:hover::before {
  opacity: 1;
}

