.input-group {
  display: flex;
  align-items: center;
  margin-bottom: 2vh;
  flex-wrap: nowrap;
  margin-top: 2vh;
}

.input-group label {
  flex: 0 0 210px;
  white-space: nowrap;
}

label {
  color: #E0E0E0;
  margin-left: 1vw;
  font-size: 1.8vh;
  font-weight: 400;
  text-align: left;
}

.input-group input {
  font-weight: 400;
  font-size: 1.6vh;
  text-align: left;
  font-family: 'Roboto';
  border-radius: 6px;
  background-color: #0E1629;
  width: 51vw;
  height: 5vh;
  padding: 0.67vh;
  border: 1px solid #202A3F;
  box-sizing: border-box;
  margin-left: 0.5vw;
  color: #99A7BB
}

/*  autofilled (Chrome/Edge) */
.input-group input:-webkit-autofill,
.input-group input:-webkit-autofill:hover,
.input-group .field input:-webkit-autofill:focus {
  font-size: 1.6vh !important;     /* <- force same size */
  font-family: 'Roboto' !important;
  caret-color: #99A7BB !important; 
  -webkit-text-fill-color: #99A7BB !important;
  transition: background-color 9999s ease-out 0s;
}

/* Firefox */
.input-group input:-moz-autofill {
  font-size: 1.6vh !important;
  font-family: 'Roboto' !important;
  box-shadow: 0 0 0 1000px #0E1629 inset !important;
  -moz-text-fill-color: #99A7BB !important;
  caret-color: #99A7BB !important;
}

.input-group input:focus {
  border: 1px solid rgba(1, 123, 205, 0.24);
  outline: none;
}

.input-group input[type="date"] {
  width: 80%;
}

