@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&display=swap');

#vesselBoxContainer {
  /* margin-top: 2vh; */
  padding-right: 5px;
  position: fixed;
  top: 50%;
  left: 49%;
  transform: translate(-50%, -50%);
  width: 86%;
  height: 79%;
  /* background-color:#090F1C; */
  backdrop-filter: blur(2px);
  z-index: 9999;
  display: block;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.vessel-BlueBox {
  display: none;
  position: fixed;
  width: 100%;
  height: 10%;
  /* height: 20%; */
  z-index: 9999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Overlay-page {
  width: 30.5vw;
  height: 7.1vh;
  display: flex;
  margin-right: 51vw;
  left: 11%;
  flex-direction: column;
  gap: 0.83vw;
}

.breadcrumb {
  width: 31.4vw;
  height: 1.1vh;
  font-size: 1.5vh;
  font-weight: 400;
  color: #5A6D96;
  display: flex;
  gap: 0.83vw;
  font-family: 'Roboto';
}

.breadcrumb-link {
  color: #3FA0E4;
  text-decoration: none;
}

.breadcrumb-link:hover {
  text-decoration: underline;
  color: #3FA0E4
}

.breadcrumb-blue {
  color: #3FA0E4;
}

.head {
  width: 20.9vw;
  height: 5.2vh;
  display: flex;
  align-items: center;
  border-radius: 8px;
  font-weight: 400;
  color: white;
}

.back-arrow {
  display: flex;
  align-items: center;
  z-index: 10000;
}

.back-icon {
  width: 1.8vw;
  height: 3.6vh;
  padding: 0.3vw;
  border-radius: 50%;
  background-color: rgba(93, 120, 180, 0.08);
  margin-right: 1vw;
}

.name {
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 2.8vh;
}


.button {
  /* margin-top: -5.5vh; */
  margin-top: -6.5vh;
  margin-left: 74.3vw;
  padding: 1vh 1vw;
  font-size: 1.8vh;
  font-weight: 500;
  border: none;
  background-color: #017BCD;
  color: white;
  border-radius: 30px;
  cursor: pointer;
  display: flex;
  text-align: center;
  font-family: 'Inter';
  gap: 0.3vw;
}

div#loadingcard {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 26.6vw;
  justify-content: center;
  padding: 16vh 1vw;
  background-color: #0C1323;
}

#fileProgress {
  width: 14.5vw;
  padding: 1vh;
  border-radius: 5px;
}

#fileProgress::-webkit-progress-bar {
  background-color: #f3f3f3;
  border-radius: 5px;
}

#fileProgress::-webkit-progress-value {
  background-color: #4e8dff;
  border-radius: 5px;
}

#loadingcard p {
  font-family: 'Inter';
  color: #FFFFFF;
  font-weight: 600;
  font-size: 2.2vh;

}

#loadingcard .secondmessage {
  font-family: 'Inter';
  color: #7B869D;
  font-weight: 400;
  font-size: 1.6vh;
}

.Upload {
  width: 1.25vw;
  height: 2.52vh;
}

#vesselBox {
  margin-left: 0vw;
  max-height: 75vh;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
  /* margin-top: 14vh; */
  margin-top: 9vh;
}

::-webkit-scrollbar {
  width: 6px;
}

#vesselBox::-webkit-scrollbar-thumb {
  background-color: #3FA0E4;
  /* Your desired color */
}

.vesseltable {
  width: 100%;
}

.vesseltable tbody {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.vesseltable tr {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 25%;
  /* margin-bottom: 2vh; */
}

.vesseltable td {
  width: 100%;
  text-align: center;
}

.card-container {
  position: relative;
  display: inline-block;
  width: 17vw;
  height: 34vh;
  margin: 0.5vw;
  border-radius: 3vh;
  border: 1px solid #2B3E68;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background: #131E36;
  transition: transform 0.3s, box-shadow 0.3s;
}

.card-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.card-button img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.card-button div {
  padding: 1vw;
  background-color: #172440;
  font-size: 1.7vh;
  font-weight: 400;
}

.deleteVesselButton {
  position: absolute;
  top: 3%;
  right: 0.4vw;
  background-color: #182440;
  /* background-image: url('/static/img/trash.png'); */
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 50%;
  width: 1.8vw;
  height: 3.6vh;
  cursor: pointer;
  font-size: 1.2vh;
  line-height: 1.5vw;
  text-align: center;
  padding: 0;
}

.custom-modal-btn {
  content: attr(data-name);
  position: relative;
  background: #1D2B49;
  color: #DEEAFD;
  justify-content: left;
  align-items: center;
  text-decoration: none;
  font-size: 2.4vh;
  font-weight: 300;
  padding-left: 10px;
  width: 27vw;
  height: 7.5vh;
  border-radius: 50px;
  display: flex;
  overflow: hidden;
  transition: all 0.3s ease-in;
  margin-bottom: 2vh;
}

.custom-modal-btn:hover {
  transform: translateY(-5px);
  background-color: #017BCD;
}

.vessel-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.card-button {
  border: none;
  background: none;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.vesselButton {
  text-align: center;
  padding-top: 1.5vh;
  padding-bottom: 0.6vh;
  border-radius: 3vh;
  margin-bottom: 0.7vh;
  margin-right: 0.4vw;
  margin-left: 0.4vw;
  color: rgb(222, 234, 253);
  font-family: Roboto;
  height: 4.9vh;
}

/* Fullscreen overlay */
.custommodal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* Modal box */
.custom-modal-content {
  background-color: #0E1629;
  color: #DEEAFD;
  margin: 15% auto;
  padding: 0;
  text-align: center;
  position: relative;
  max-width: 400px;
  z-index: 10015;
}

.custom-modal-body {
  padding: 20px;
}

/* Buttons */
.custom-modal-footer {
  display: flex;
  display: flex;
  margin-left: 45%;
}

.modal-btn {
  width: 7vw;
  padding-left: 1.9vw;
  margin: 0.5vw;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  font-size: 1.4vh;
  height: 3vh;
}

.confirm-btn {
  background-color: #017BCD;
  color: #DEEAFD;
}

.cancel-btn {
  background-color: #017BCD;
  color: #DEEAFD;
}

.confirm-btn:hover {
  background-color: #1d4ed8;
}

.cancel-btn:hover {
  background-color: #475569;
}

.custom-modal-body p {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Inter';
  font-weight: 400;
}

.custom-modal-body p::before {
  margin-right: 10px;
  font-size: 18px;
}

.warning-icon {
  font-size: 24px;
  color: #f39c12;
  margin-right: 10px;
}

