#loadingIndicator {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(9, 15, 28, 0.81);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10031;
  font-size: 24px;
  border-radius: 27.5px 27.5px 0 0;
}

#loadingvessel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(9, 15, 28, 0.81);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  font-size: 24px;
