#vessel-database {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width:100vw;
    align-items: center;
    justify-content: center; 
    background-color: #090F1C;
 
}

#vessel-table-div {
    margin-bottom: 7vh;
    width: 83.5%;
    display: flex;
    justify-content: center;   
    flex-direction: column;
}

.Vessel-top-box {
    width: 24vw;
    height: 7.1vh;
    display: flex;
    margin-right: 49.5vw;
    font-family: 'Inter';
    left: 11%;
    flex-direction: column;
    gap: 0.83vw;
}

.breadcrumb {
    width: 31.4vw;
    height: 1.1vh;
    font-size: 1.5vh;
    font-weight: 400;
    color: #5A6D96;
    display: flex;
    gap: 0.83vw;
    margin-left: 0.5vw;
    font-family: 'Roboto';
}

.breadcrumb-link {
    color: #3FA0E4;
    text-decoration: none;
}

.breadcrumb-link:hover {
    text-decoration: underline;
    color:#3FA0E4
}

.breadcrumb-blue {
    color:#3FA0E4;

}

#vesselName {
    font-weight: 400;
    font-size: 2.5vh;
}

.header {
    width: 20.9vw;
    /* height: 5.2vh; */
    display: flex;
    align-items: center;
    border-radius: 8px;
    font-weight: 400;
    color: white;
}

.back-arrow {
    display: flex;
    align-items: center;
    z-index: 1000;
}

.back-icon {
    width: 1.3vw;
    height: 2.6vh;
    padding: 0.3vw;
    border-radius: 50%;
    background-color: rgba(93, 120, 180, 0.08);
    margin-right: 1vw;
}

#content-container {
    display: flex;
    justify-content: space-between;
    width: 90.5%;
    padding-top: 1vh;
    padding-bottom: 1vh;
    padding-right: 6vw;
    padding-left: 1vw;
    border-radius: 0px 0px 30px 30px;
    font-family: 'Inter';
}

#model-container {
    height: 61.91vh;
    margin-right: 4vw;
    margin-left: 2.7vw;
    position: relative;
    width: 32.396vw;
    overflow: hidden;
}

#model-container canvas {
    border-radius: 30px;
}

#vessel-data-table{
    flex: 1;
    border-collapse: collapse;
    width: 100%;  
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.2);
}

#vessel-data-table td {
    font-size: 1.5vh;
    text-align: center;
    color: #E0E0E0;
    padding: 0.38vh 0.4vw;
}

#vessel-data-table th {
    font-size: 2.6vh;
    text-align: left;
    font-weight: 400;
    padding: 1vh 0.4vw;
}

#vessel-data-table tr td:first-child {
    color: #E0E0E0;
    font-weight: 400;
    font-size: 2vh;
    line-height: 185%;
    padding-left: 1.5vw;
    width: 45%;
    text-align: left;
}

#vessel-data-table tr td:last-child {
    color: #99A7BB;
    font-weight: 400;
    font-size: 1.8vh;
    text-align: left;
    line-height: 185%;
}

#Vesselheader{
    color: white;
}
#vessel-data-table thead tr {
    color: white;
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: #090F1C;
}

#vessel-data-table tbody tr:nth-child(odd) {
    background-color:#0E1629; 
}

#vessel-data-table tbody tr:nth-child(even) {
    background-color: rgba(14, 22, 41, 0.1); 

}

#vessel-data-table tbody td {
    vertical-align: middle;
}

#button-container {
    top: 0%;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 3vw;
    border-top-left-radius: 6px;
    padding-top: .8vh;
    padding-bottom: .8vh;
}

#button-container button {
    background: #223151;
    border-radius: 21px;
    width: 2vw;
    height: 2vw;
    margin: 0.5vh;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.2vw solid transparent;
    font-size: 1.6em; 
    position: relative;
}
img#view {
    transform: scale(1.5);
}

#buttonContainer button.active {
    border: 1px solid #017BCD;
}

/* Tooltip bubble */
#button-container button::after {
  content: attr(data-tooltip);
  position: absolute;
  left: 130%;  /* appear to the right side of button */
  top: 50%;
  transform: translateY(-50%);
  background-color: #223151;
  color: #DEEAFD;
  font-family: 'Roboto';
  padding: 4px 8px;
  border-radius: 4px;
  white-space: nowrap;
  font-size: 1.2vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out;
  z-index: 10;
}

#button-container button:hover::after,
#button-container button:hover::before {
  opacity: 1;
}
