.content {
  background-color: #090F1C;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0 10%;
  min-height: 100vh;
  position: relative;
}

.left-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  width: 50%;
  margin-top: 20vh;
}

