}

#toggleVesselButton2, #toggleVesselButton3, #IntactStability4, 
#IntactStability5, #IntactStability6, #IntactStability7, #toggleVesselButton4, #toggleVesselButton5, #toggleVesselButton6 {
  position: relative;
  opacity: 0.5; 
  pointer-events: none; 
}

#IntactStability2 .lock-icon, #IntactStability3 .lock-icon, #toggleVesselButton2 .lock-icon, #toggleVesselButton3 .lock-icon, #IntactStability4 .lock-icon, 
#IntactStability5 .lock-icon, #IntactStability6 .lock-icon, #IntactStability7 .lock-icon, #toggleVesselButton4 .lock-icon, #toggleVesselButton5 .lock-icon,
 #toggleVesselButton6 .lock-icon {
  position: absolute;
  top: 50%;
  right: 0.8vw;
  transform: translateY(-50%);
  font-size: 18px;
  opacity: 1;
}

#toggleVesselButton2::before {
  content: "";
  position: absolute;
  width: 400%;
  height: 400%;
  border-radius: 5px;
}


@keyframes rotateFadeToggleVesselButton2 {
  0% {
    transform: rotate(0deg);
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    transform: rotate(360deg);
    opacity: 0;
  }
}

