.home_button{
  content: attr(data-name);
  position: relative;
  background: #1D2B49;
  color: #DEEAFD;
  justify-content: left;
  align-items: center;
  text-decoration: none;
  font-size: 2.4vh;
  font-weight: 300;
  padding-left: 10px;
  width: 27vw;
  height: 7.5vh;
  border-radius: 50px;
  display: flex;
  overflow: hidden;
  transition: all 0.3s ease-in;
  margin-bottom: 2vh; 
}

.home_button:hover {
  transform: translateY(-5px);
  background-color: #017BCD;
}
