button#jackup_data,
button#crane_data,
#crane_data {
    font-family: 'Inter';
    background-color: #0B2747;
    color: #DEEAFD;
    border: none;
    outline: none;
    padding: 0.7em 1.14em;
    font-size: 1.4vh;
    font-weight: 500;
    cursor: pointer;
    transition: color 0.3s ease;
    position: relative;
    outline: none;
}

button#jackup_data.active::after,
button#crane_data.active::after {
    font-family: 'Inter';
    content: "";
    position: absolute;
    left: 0;
    bottom: -3px;
    width: 100%;
    height: 2px;
    color: #FFFFFF;
    background-color: #017BCD;
}

button#jackup_data.active,
button#crane_data.active {
    background-color: #071A30;
    color: white;
