* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;

}
.no-global-styles * {
  box-sizing: content-box;
  margin: revert;
  padding: revert;
  

}
body {
  font-family: 'Roboto';
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #090F1C;
  outline: none;
}

html, body {
  display: flex; 
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
  overflow-y: hidden;
  outline: none;
}