@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&display=swap');


.body-split {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: relative;
}

.body-split .left-side {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0;
}

.body-split .left-side::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, #08173A, #1d2b49);
  background-position: -42vw -70%;
  background-blend-mode: overlay;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tagline {
  width: 32vw;
  height: 20vh;
  position: fixed;
  left: 4vw;
  top: 11.5vh;
  z-index: 999;
}

.fusie-logo {
  height: 7.346vh;
  width: 9.427vw;
}

.tagline h2 {
  color: #DEEAFD;
  font-weight: 600;
  font-size: 5.6vh;
  line-height: 6vh;
}

#leftcard {
  position: relative;
  z-index: 2;
  width: 50vw;
  height: 56vh;
  margin-top: 17vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.ship-image {
  width: 120vw;
  object-fit: contain;
  position: absolute;
  margin-top: 23vh;
  margin-left: 3vw;
}

@media (min-width: 1024px) {

  .ship {
    width: 100%;
    max-width: 500px;
    display: block;
    margin: 0 auto;
  }

  h1 {
    color: #0084AC;
    font-family: Yu Gothic UI;
    font-size: 6vh;
    font-weight: 500;
    margin-bottom: 9.1vh;
  }

  h2 {
    color: #0084AC;
    font-family: Yu Gothic UI;
    font-size: 1.8vh;
    margin-bottom: 2.1vh;
  }

  #h5-1 {
    margin-top: -7.7vh;
    color: #003748;
    font-family: Yu Gothic UI;
    font-size: 1.5vh;
    font-weight: 500;
    margin-left: 0.5vw;
  }

  #h5-2 {
    margin-top: 1vh;
    color: #003748;
    font-family: Yu Gothic UI;
    font-size: 1.5vh;
    font-weight: 500;
  }

  h5 {
    color: #618491;
    font-family: Yu Gothic UI;
    font-size: 1.5vh;
    font-weight: 100;
