}

.left-side-heading {
  font-size: 2.8vw;
  font-weight: 700;
  color: white;
  margin-bottom: 2vh;
}

.logo-container-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  position: fixed;
  top: 86.7vh;
  left: 9vh;
  z-index: 1000;
}

.marine-ops-loogo {
  height: 6.96vh;
  width: 9.424vw;
}

.body-split .right-side {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #090F1C;
}

.title.login {
  margin-top: 4vh;
  margin-bottom: 3vh;
}

.title.signup {
  margin-bottom: 0.8vh;
  margin-top: 10vh;
}

.welcome-text {
  font-family: 'Inter';
  font-size: 3vh;
  font-weight: 500;
  color: #DEEAFD;
  margin-bottom: 1vh;
}

.signup-form {
  display: none;
}

.signup-text {
  font-family: 'Inter';
  font-size: 1.4vh;
  color: #DEEAFD;
  font-weight: 300;
}

.signup-text a {
  font-family: 'Inter';
  font-size: 1.4vh;
  color: #0396EA;
  font-weight: 400;
  text-decoration: none;
  margin-left: 0.2vw;
}

.options {
  display: flex;
  justify-content: space-between;
  font-size: 1.4vh;
  margin-top: -0.7vh;
}


.options label {
  color: #FFFFFF;
  margin-left: 0.1vw;
  font-family: 'Inter';
  font-size: 1.3vh;
  font-weight: 300;
  text-align: left;
}

.options a {
  font-family: 'Inter';
  font-size: 1.3vh;
  color: #FFFFFF;
  font-weight: 300;
  text-decoration: none;
  margin-right: 0.1vw;
}

.wrapper-container {
  display: block;
  position: relative;
  margin: auto;
  padding: 3vw;
}

.wrapper {
  height: 50vh;
  overflow: hidden;
}

.wrapper .title {
  font-size: 0.5vh;
  font-weight: 600;
  text-align: center;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

#Transafe {
  width: 78%;
  height: 101%;
  margin-top: 2.5vh;

}

.wrapper .form-container {
  height: 33vh;
  overflow: hidden;
}

::selection {
  background: #1a75ff;
  color: #fff;
}

.wrapper .slide-controls {
  position: relative;
  display: flex;
  width: 100%;
  overflow: hidden;
  margin: 3.7vh 0 2vh 0;
  justify-content: space-between;
  border: 1px solid lightgrey;
  border-radius: 15px;
}

input[type="radio"] {
  display: none;
}

#login:checked~label.login {
  cursor: default;
  user-select: none;
}

#login:checked~label.signup {
  color: #000;
}

.slide-controls label.signup {
  color: #000;
}

.slide-controls .slide {
  height: 100%;
  width: 100%;
  color: white;
  font-size: 1.8vh;
  font-weight: bold;
  text-align: center;
  line-height: 8vh;
  cursor: pointer;
  z-index: 1;
  transition: all 0.6s ease;
}

.slide-controls .slider-tab {
  position: absolute;
  height: 100%;
  width: 50%;
  left: 0;
  z-index: 0;
  border-radius: 15px;
  background-color: #515AFF;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.form-container .form-inner {
  display: flex;
  width: 100%;
  height: 100%;
}

.form-container .form-inner form {
  width: 100%;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.form-inner form .field {
  height: 5vh;
  width: 100%;
  background-color: #10192D;
  border-radius: 5.8vh;
}

.form-inner form .field input {
  height: 100%;
  width: 100%;
  outline: none;
  padding-left: 6.5vh;
  border-radius: 5.8vh;
  border: 1px solid lightgray;
  font-size: 1.4vh;
  font-weight: 400;
  transition: all 0.3s ease;
  font-family: 'Inter';
  color: #78839B;
  background-color: #10192D;
  border: none;
  /* margin-top: -0.2vh; */
}

/*  autofilled (Chrome/Edge) */
.form-inner form .field input:-webkit-autofill,
.form-inner form .field input:-webkit-autofill:hover,
.form-inner form .field input:-webkit-autofill:focus {
  font-size: 1.4vh !important;     /* <- force same size */
  font-family: 'Inter' !important;
  -webkit-text-fill-color: #78839B !important;
  caret-color: #78839B !important;
  /* -webkit-box-shadow: 0 0 0 1000px #10192D inset !important;  */
  transition: background-color 9999s ease-out 0s;
}

/* Firefox */
.form-inner form .field input:-moz-autofill {
  font-size: 1.4vh !important;
  font-family: 'Inter', system-ui, sans-serif !important;
  box-shadow: 0 0 0 1000px #10192D inset !important;
  -moz-text-fill-color: #78839B !important;
  caret-color: #78839B !important;
}

/* optional: keep placeholder size consistent */
.form-inner form .field input::placeholder {
  font-size: 1.4vh;
}

#email::placeholder {
  color: #78839B;
}

#password::placeholder {
  color: #78839B;
}

.form-inner form .pass-link {
  margin-top: 3vh;
}

.form-inner form .pass-link a,
.form-inner form .signup-link a {
  color: #1a75ff;
  text-decoration: none;
}

.form-inner form .pass-link a:hover,
.form-inner form .signup-link a:hover {
  text-decoration: underline;
}

.form-inner form .field input:focus {
  background-color: #10192D;
}

.form-inner form .field input::placeholder {
  color: #999;
  transition: all 0.3s ease;
}

form .field input:focus::placeholder {
  color: #0084AC;
}

form .btn {
  height: 5.8vh;
  width: 100%;
  border-radius: 5.8vh;
  position: relative;
  overflow: hidden;
  padding-left: 0;
  margin-bottom: 2vh;
}

form .btn .btn-layer {
  height: 100%;
  width: 300%;
  position: absolute;
  left: -100%;
  border-radius: 15px;
  transition: all 0.4s ease;
}

form .btn:hover .btn-layer {
  left: 0;
}

form .btn input[type="submit"] {
  background-color: #017BCD;
  height: 100%;
  width: 100%;
  z-index: 1;
  position: relative;
  border: none;
  color: #fff;
  padding-left: 0;
  border-radius: 5.8vh;
  font-size: 1.6vh;
  font-weight: 500;
  cursor: pointer;
  padding-bottom: 0.3vh;
}


.form-inner form .signup-link {
  text-align: center;
  margin-top: 0.5vh;
  font-size: 1.4vh;
  font-weight: 400;
  font-family: 'Inter';
}

#signup:checked~.slider-tab {
  left: 50%;
}

#signup:checked~label.signup {
  cursor: default;
  user-select: none;
  color: white;
}

#signup:checked~label.login {
  color: #000;
}

.error-message {
  color: #F6665C;
  font-size: small;
  font-family: 'Inter';
  margin-top: 1.2vh;
  font-size: 1.4vh;
  font-weight: 300;
}

.otp-message {
  color: #2FACFF;
  font-family: 'Inter';
  margin-top: 1.2vh;
  font-size: 1.4vh;
  font-weight: 300;
}

.contact-info {
  display: inline-block;
  padding: 1.5vh 3vh;
  background-color: #0C1426;
  color: #DEEAFD;
  text-align: center;
  font-size: 1.6vh;
  font-weight: bold;
  text-decoration: none;
  border-radius: 5.8vh;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 2.5vh;
}

.contact-info .contact-link {
  color: #0091E4;
  font-family: 'Inter';
  font-weight: 400;
  font-size: 1.4vh;
  text-decoration: underline;
  transition: color 0.3s ease;
}

.contact-info .contact-link:hover {
  color: #00BFFF;
  text-decoration: none;
}

.contact-info p {
  margin: 0;
  color: #DEEAFD;
  font-size: 1.4vh;
  font-weight: bold;
  font-weight: 300;
  font-family: 'Inter';
  line-height: 2.2vh;
  text-align: center;
}

.grey-text {
  color: #DEEAFD;
  font-size: 1.4vh;
  font-weight: 400;
  font-family: 'Inter';
  margin-right: 0.2vw;
}

.link-button {
  color: #0396EA;
  font-size: 1.4vh;
  font-weight: 400;
  font-family: 'Inter';
  background: transparent;
  border: none;
}

.input-with-icon {
  position: relative;
  margin-top: 1.9vh;
}

.input-without-icon {
  margin-top: 2.4vh;
}

.input-icon {
  position: absolute;
  top: 50%;
  left: 2vh;
  transform: translateY(-50%);
  height: 2vh;
  width: 2vh;
  pointer-events: none;
  opacity: 0.7;
}

.input-lock-icon {
  position: absolute;
  top: 50%;
  left: 2vh;
  transform: translateY(-50%);
  height: 1.7vh;
  width: 1.5vh;
  pointer-events: none;
  opacity: 0.7;
}


.input-with-icon input {
  padding-left: 6vh;
}

.checkbox-wrapper {
  display: inline-flex;
  align-items: center;
  position: relative;
  padding-left: 2.5vh;
  cursor: pointer;
  font-family: Yu Gothic UI;
  font-size: 1.4vh;
  font-weight: 400;
  color: #FFFFFF;
  user-select: none;
}

.checkbox-wrapper input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.custom-check {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 1.6vh;
  width: 1.6vh;
  background-color: transparent;
  border: 1px solid #ccc;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}

.checkbox-wrapper input:checked+.custom-check {
  background-color: #0091E4;
  border: none;
}

.custom-check::after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox-wrapper input:checked+.custom-check::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30%;
  height: 60%;
  border: solid #000000;
  /* or black depending on contrast */
  border-width: 0 2px 2px 0;
  transform: translate(-50%, -50%) rotate(45deg);
}

@media (max-width: 768px) {

  html,
  body {
    height: auto;
    overflow-y: auto;
  }

  .body-split {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: auto;
    overflow-x: hidden;
  }

  .left-side {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 4vh 5vw 2vh;
    box-sizing: border-box;
    position: relative;
    background: linear-gradient(to bottom, #08173A, #1d2b49);
    overflow: hidden;
  }

  .right-side {
    width: 100vw;
    min-height: 100vh;
    background-color: #090F1C;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 4vh 5vw;
    box-sizing: border-box;
  }

  .tagline {
    width: 100%;
    text-align: center;
    margin-top: 8vh;
    margin-bottom: 2vh;
    padding: 0 2vw;
    position: relative;
    left: 0;
  }

  .tagline h2 {
    font-size: clamp(24px, 10.5vw, 34px);
    line-height: 1.5;
    font-weight: 600;
    color: #DEEAFD;
    margin: 0;
  }

  #leftcard {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    margin-top: 0vh;
  }

  .ship-image {
    width: 100%;
    max-width: none;
    height: auto;
    object-fit: contain;
    position: relative;
    display: block;
    transform: scale(3.2);
    margin-left: 13vw;
  }

  .logo-container-bottom {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    margin-bottom: 2vh;
    box-sizing: border-box;
    left: 0vh;
    top: 93vh;
  }

  .fusie-logo {
    width: clamp(80px, 25vw, 120px);
    height: auto;
  }

}

@media (min-width: 1025px) {
  .form-container {
    height: auto;
    width: 21vw;
  }
}